.post-grid {
	display: grid;
	gap: 20px;
	grid-template-columns: repeat(3, minmax(0, 1fr));

	.grid-card {

		background: #F5F5F5;
		border-color: #dcdcde;
		border-style: solid;
		border-width: 0px 0px 0px 0px;
		border-radius: 4px 4px 4px 4px;
		box-shadow: 0px 3px 6px 0px #00000026;


		.content-body {
			padding: 0px 12px 24px 12px;
			margin: 0px 0px 0px 0px;

			.post-grid-title h5 {
				text-align: center;

				a {
					font-size: 18px;
					font-style: normal;
					font-weight: 600;
					text-transform: none;
					text-decoration: none;
					line-height: 24px;
					color: #1A261F;
					margin: 12px 0px 0px 0px;
				}



			}

			.post-grid-meta {

				font-size: 14px;
				font-style: normal;
				font-weight: 400;
				text-transform: none;
				text-decoration: none;
				line-height: 20px;
				color: #7B7878;
				text-align: center;
				margin: 10px 0px 0px 0px;

			}

			.post-grid-excerpt {
				font-size: 16px;
				font-style: normal;
				font-weight: 400;
				text-transform: none;
				text-decoration: none;
				line-height: 28px;
				color: #1A261F;
				margin: 20px 0px 0px 0px;
				text-align: center;

			}

			.post-grid-btn a {
				font-size: 16px;
				font-style: normal;
				font-weight: 400;
				text-transform: none;
				text-decoration: none;
				line-height: 24px;
				color: #FFFFFF;
				box-shadow: none;
				padding: 8px 16px 8px 16px;
				margin: 20px 0px 0px 0px;
				border-color: #DCDCDE;
				border-style: solid;
				border-width: 0px 0px 0px 0px;
				border-radius: 4px 4px 4px 4px;
				background: #0053F0;
			}
		}


	}
}


.pagecrafter-tab-panel .components-tab-panel__tabs {
	margin-bottom: 10px;

	.is-active {
		background-color: #ffffff !important;
		color: #008db4 !important;
	}

	.components-tab-panel__tabs-item {
		background-color: #008db4;
		color: #ffffff;

	}

}